import React, { useState, useEffect, useCallback } from 'react';
import { useParams, useNavigate, useLocation, matchPath } from 'react-router-dom';
import { Helmet } from 'react-helmet';

import Navbar from '../components/layout/Navbar';
import EggRatesTable from '../components/rates/EggRatesTable';
import Footer from '../components/layout/Footer';
import MainContent from '../components/rates/MainContent';
import StatePage from '../components/rates/StatePage';
import StateList from '../components/rates/StateList';
import BlogList from '../components/blog/BlogList';
import FAQ, { generateFaqSchema } from '../components/common/FAQ';
import Breadcrumb from '../components/layout/Breadcrumb';
import blogs from '../data/blogs';

const MainPage = () => {
  const { city: cityParam, state: stateParam } = useParams();
  const navigate = useNavigate();
  const location = useLocation();
  const [states, setStates] = useState([]);
  const [cities, setCities] = useState([]);
  const [selectedState, setSelectedState] = useState(stateParam ? stateParam.replace('-egg-rate', '') : '');
  const [selectedCity, setSelectedCity] = useState(cityParam ? cityParam.replace('-egg-rate', '') : '');
  const [eggRates, setEggRates] = useState([]);  /* eslint-disable-next-line no-unused-vars */
  const displayName = selectedCity ? selectedCity : selectedState ? selectedState : 'India';

  // Fetch states on mount
  useEffect(() => {
    fetch('/php/api/location/get_states.php')
      .then(res => res.json())
      .then(data => setStates(data))
      .catch(error => console.error('Error fetching states:', error));
  }, []);

  // Fetch cities when state is selected
  useEffect(() => {
    if (selectedState) {
      fetch(`/php/api/location/get_cities.php?state=${selectedState}`)
        .then(res => res.json())
        .then(data => setCities(data))
        .catch(error => console.error('Error fetching cities:', error));
    }
  }, [selectedState]);

  const handleFetchRates = useCallback(() => {
    const endpoint = selectedCity && selectedState 
      ? `/php/api/rates/get_rates.php?city=${selectedCity}&state=${selectedState}`
      : '/php/api/rates/get_latest_rates.php';

    fetch(endpoint)
      .then(res => res.json())
      .then(data => {
        const convertedData = data.map(item => ({
          ...item,
          rate: parseFloat(item.rate),
        }));
        setEggRates(convertedData);
      })
      .catch(error => console.error('Error fetching rates:', error));
  }, [selectedCity, selectedState]);

  // Fetch rates when component mounts or when city/state changes
  useEffect(() => {
    handleFetchRates();
  }, [handleFetchRates]);

  // Check if the current URL matches /state/:state
  const stateMatch = matchPath(
    { path: '/state/:state' },
    location.pathname
  );

  // URL update logic with debouncing
  const updateURL = useCallback(() => {
    const currentPath = location.pathname.endsWith('/') 
      ? location.pathname.slice(0, -1) 
      : location.pathname;
    
    if (selectedCity && !currentPath.includes(`-egg-rate`)) {
      const newPath = `/${selectedCity.toLowerCase()}-egg-rate`;
      if (currentPath !== newPath) {
        navigate(newPath, { replace: true });
      }
    } else if (selectedState && !selectedCity && !currentPath.includes(`-egg-rate`)) {
      const newPath = `/state/${selectedState.toLowerCase()}-egg-rate`;
      if (currentPath !== newPath) {
        navigate(newPath, { replace: true });
      }
    }
  }, [selectedCity, selectedState, navigate, location.pathname]);

  // URL update effect with debouncing
  useEffect(() => {
    const timeoutId = setTimeout(() => {
      updateURL();
    }, 50);
    
    return () => clearTimeout(timeoutId);
  }, [updateURL]);

  // Update selected state and city when URL parameters change
  useEffect(() => {
    if (stateParam) {
      const newState = stateParam.replace('-egg-rate', '');
      if (selectedState !== newState) {
        setSelectedState(newState);
        setSelectedCity('');
      }
    }
    if (cityParam) {
      const newCity = cityParam.replace('-egg-rate', '');
      if (selectedCity !== newCity) {
        setSelectedCity(newCity);
      }
    }
    if (!stateParam && !cityParam && (selectedState || selectedCity)) {
      setSelectedCity('');
      setSelectedState('');
    }
  }, [stateParam, cityParam, selectedState, selectedCity]);

  // SEO metadata
  const formattedDate = new Date().toLocaleDateString('en-US', {
    day: 'numeric',
    month: 'long',
    year: 'numeric'
  });

  const getSeoTitle = () => {
    if (selectedCity) {
      return `Egg Rate in ${selectedCity}, ${selectedState} Today (${formattedDate})`;
    }
    if (selectedState) {
      return `${selectedState} Egg Rate Today - NECC Price (${formattedDate})`;
    }
    return `Egg Rate Today - Check Today's NECC Egg Price in India (${formattedDate})`;
  };

  const getSeoDescription = () => {
    if (selectedCity) {
      return `Check today's egg rate in ${selectedCity}, ${selectedState}. Get latest NECC egg prices, market trends, and wholesale rates updated daily.`;
    }
    if (selectedState) {
      return `Today's egg rate in ${selectedState}. Compare NECC egg prices across cities, check market rates, and track daily price changes.`;
    }
    return "Get today's egg rates across India. Check NECC egg prices, compare city rates, and track market trends. Updated daily with wholesale and retail prices.";
  };

  const structuredData = {
    '@context': 'https://schema.org',
    '@type': 'Product',
    name: 'Chicken Eggs',
    description: getSeoDescription(),
    offers: {
      '@type': 'AggregateOffer',
      priceCurrency: 'INR',
      lowPrice: Math.min(...eggRates.map(rate => rate.rate)),
      highPrice: Math.max(...eggRates.map(rate => rate.rate)),
      offerCount: eggRates.length,
      priceValidUntil: new Date(new Date().setDate(new Date().getDate() + 1)).toISOString()
    }
  };

  return (
    <>
      <Helmet>
        <title>{getSeoTitle()}</title>
        <meta name="description" content={getSeoDescription()} />
        <link rel="canonical" href={`https://todayeggrates.com${location.pathname}`} />
        <script type="application/ld+json">{JSON.stringify(structuredData)}</script>
        <script type="application/ld+json">
          {JSON.stringify(generateFaqSchema(selectedCity, selectedState, eggRates))}
        </script>
        <meta property="article:modified_time" content={new Date().toISOString()} />
        <meta property="og:title" content={getSeoTitle()} />
        <meta property="og:description" content={getSeoDescription()} />
        <meta property="og:url" content={`https://todayeggrates.com${location.pathname}`} />
        <meta property="og:type" content="website" />
        <meta property="og:image" content="https://todayeggrates.com/eggpic.webp" />
        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:title" content={getSeoTitle()} />
        <meta name="twitter:description" content={getSeoDescription()} />
        <meta name="twitter:image" content="https://todayeggrates.com/eggpic.webp" />
      </Helmet>

      <div className="bg-gray-50 min-h-screen flex flex-col">
        <Navbar
          selectedState={selectedState}
          setSelectedState={setSelectedState}
          setSelectedCity={setSelectedCity}
          selectedCity={selectedCity}
          states={states}
          cities={cities}
        />

        <div className="flex-grow p-4 md:p-8 lg:p-12">
          <div className="max-w-4xl mx-auto mb-4">
            <Breadcrumb />
          </div>

          <MainContent 
            selectedCity={selectedCity} 
            selectedState={selectedState}
            eggRates={eggRates}
          />

          <div className="bg-white rounded-lg shadow-md p-6">
            {stateMatch ? (
              <StatePage />
            ) : (
              <EggRatesTable 
                mode={selectedCity ? 'rate' : 'default'}
                selectedCity={selectedCity}
                selectedState={selectedState}
                eggRates={eggRates}
              />
            )}
            
            <StateList states={states} cities={cities} />
            
            <EggRatesTable mode="special" />
          </div>

          <BlogList blogs={blogs} selectedCity={selectedCity} selectedState={selectedState} />
          <FAQ selectedCity={selectedCity} selectedState={selectedState} eggRates={eggRates} />
        </div>

        <Footer />
      </div>
    </>
  );
};

export default MainPage;